export default (popupSelector, openBtnSelector, closeBtnSelector, hidingDuration = 480) => {
    let $popups = $(popupSelector),
        $document = $(document),
        $body = $('body'),
        currentTimeout;
    
    $document.on('click', openBtnSelector, function() {
        let popupId = $(this).data('popupId'),
            $popup = $popups.filter('#' + popupId);

        if ($popup && $popup.length) {
            $popups.removeClass("active");
            $popup.addClass("active");
            $body.addClass("hidden-scroll");

            return false;
        }
    });

    $document.on('click', closeBtnSelector, function() {
        let $popup = $(this).closest(popupSelector);
        $popup.addClass("hiding");
        $body.removeClass("hidden-scroll");
        
        clearTimeout(currentTimeout);
        currentTimeout = setTimeout(() => {
            $popup.removeClass("hiding");
            $popup.removeClass("active");
        }, hidingDuration);

        return false;
    });
}


