// global style
import "./index.scss";

// polyfills
// import "babel-polyfill";
// import requestanimationframe from './polyfills/requestanimationframe';
import './polyfills/arrayFind';
import './polyfills/arrayFrom';

// helpers
// import smoothScrollingToHashInit from './helpers/smoothScrollingToHash';

// components
import TextField from './components/TextField/TextField';
import popupInit from './components/popup/popup';

// blocks
import headerInit from './blocks/header/header';
import newsInit from './blocks/news/news';
import spoilerBlockInit from './blocks/spoiler-block/spoiler-block';

$(() => {
    headerInit();
    popupInit('.overlay', '.open-popup-btn', '.popup__close-btn');
    newsInit();
    new TextField('.input--text');
    spoilerBlockInit();
});



