export default () => {
    let $spoilerBlock;
    $('.spoiler-block__header').on("click", function() {
        $spoilerBlock = $(this).closest('.spoiler-block');
        $spoilerBlock.toggleClass("active");
        $spoilerBlock.children('.spoiler-block__content').stop(true).slideToggle(320);
    });
};


